.burger {
  width: 25px;
  height: 30px;
  cursor: pointer;
  z-index: 2;
  padding-top: 10px;
  position: absolute;
  top: 90px;
  left: -100px;
}

.burger span, .burger span:before, .burger span:after {
  width: 100%;
  height: 4px;
  background: #333;
  display: block;
  position: absolute;
}

.burger span:before {
  content: "";
  transition: top .3s .3s, transform .3s cubic-bezier(.23, 1, .32, 1);
  top: -7px;
}

.burger span:after {
  content: "";
  transition: bottom .3s .3s, transform .3s cubic-bezier(.23, 1, .32, 1);
  bottom: -7px;
}

.burger span.active:before {
  content: "";
  transition: top .3s, transform .3s cubic-bezier(.68, -.55, .27, 1.55) .3s;
  top: 0;
  transform: rotate(45deg);
}

.burger span.active:after {
  content: "";
  transition: bottom .3s, transform .3s cubic-bezier(.68, -.55, .27, 1.55) .3s;
  bottom: 0;
  transform: rotate(-45deg);
}

.burger span.active {
  background: #fff0;
}

.burger__menu {
  height: auto;
  color: #ccc;
  z-index: -1;
  transition: top 1s cubic-bezier(.68, -.55, .27, 1.55), background-color 1s cubic-bezier(.68, -.55, .27, 1.55), z-index 1s cubic-bezier(.68, -.55, .27, 1.55) left 1s cubic-bezier(.68, -.55, .27, 1.55);
  background-color: #e5e5e5;
  padding: 10px;
  position: fixed;
  top: -200px;
  left: 0;
  right: 0;
}

.burger__menu.animate {
  z-index: 1;
  background-color: #e5e5e5;
  top: 0;
  left: 0;
}

.burger__container {
  width: 2000px;
  margin: 0 auto 20px;
  display: flex;
}

.burger__logo {
  margin-top: 9px;
  margin-right: 37px;
}

.burger__nav-list, .burger__search {
  align-items: flex-end;
  margin-bottom: 0;
  display: flex;
}

.burger__search {
  cursor: pointer;
  transition: transform 1s;
}

.burger__search:hover {
  transform: scale(1.2);
}

.burger__nav-list {
  margin-right: 180px;
}

.burger__nav-item {
  margin-right: 50px;
  padding: 2px;
  list-style-type: none;
  transition: transform .5s;
}

.burger__nav-item:hover {
  background-color: #fff;
  transform: scale(1.1);
}

.burger__nav-item:active {
  transform: translateY(1px);
}

.burger__nav-link {
  color: #000;
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
}

