  }

  .burger__menu {
    transition: left 1s cubic-bezier(.68, -.55, .27, 1.55), bottom 1s cubic-bezier(.68, -.55, .27, 1.55);
    top: 0;
    bottom: 0;
    left: -500px;
  }

  .burger__container {
